<template>
  <div class="lottery">
    <canvas
      id="myCanvas"
      width="600"
      height="600"
    >
      当前浏览器版本过低，请使用其他浏览器尝试
    </canvas>
    <!-- <p id="message">50元话费</p> -->
    <img
      src="../assets/start.png"
      id="start"
      @click="start"
    >
  </div>
</template>

<script>
const $ = require("jquery");
require("../utils/turntable.js");
import { debuglog } from "util";
export default {
  name: "luckCircle",
  data() {
    return {
      angel: 0,
      wheelSurf: null,
      list: {},
      throttle: true,
      initData2: []
    };
  },
  created() {},
  mounted() {
    $.ajax({
      method: "post",
      url: "/url",
      success: resp => {},
      error: err => {
        console.log("error");
        var initData = {
          success: true,
          list: [
            {
              id: 100,
              name: "5000元京东卡",
              // image: "./images/1.png",
              // rank: 1,
              weight: 10
            },
            {
              id: 101,
              name: "1000元京东卡",
              // image: "./images/2.png",
              // rank: 2,
              weight: 20
            },
            {
              id: 102,
              name: "100个比特币",
              // image: "./images/3.png",
              // rank: 3,
              weight: 5
            },
            {
              id: 103,
              name: "50元话费",
              // image: "./images/4.png",
              // rank: 4,
              weight: 20
            },
            {
              id: 104,
              name: "100元话费",
              // image: "./images/5.png",
              // rank: 5,
              weight: 20
            },
            {
              id: 105,
              name: "500个比特币",
              // image: "./images/6.png",
              // rank: 11,
              weight: 1
            },
            {
              id: 106,
              name: "500元京东卡",
              // image: "./images/7.png",
              // rank: 7,
              weight: 20
            },
            {
              id: 107,
              name: "50元话费2",
              // image: "./images/4.png",
              // rank: 8,
              weight: 5
            },
            {
              id: 108,
              name: "100元话费2",
              // image: "./images/5.png",
              // rank: 9,
              weight: 4.95
            }
          ]
        };
        this.initData2 = initData;
        this.angel = 360 / initData.list.length;
        // 格式化成插件需要的奖品列表格式
        for (var i = 0, l = initData.list.length; i < l; i++) {
          this.list[initData.list[i].id] = {
            id: initData.list[i].id,
            name: initData.list[i].name,
            image: initData.list[i].image
          };
        }
        // 定义转盘奖品
        this.wheelSurf = $("#myCanvas").WheelSurf({
          list: this.list, // 奖品 列表，(必填)
          outerCircle: {
            color: "#df1e15" // 外圈颜色(可选)
          },
          innerCircle: {
            color: "#f4ad26" // 里圈颜色(可选)
          },
          dots: ["#fbf0a9", "#fbb936"], // 装饰点颜色(可选)
          disk: [
            "#ffb933",
            "#ffe8b5",
            "#ffb933",
            "#ffd57c",
            "#ffb933",
            "#ffe8b5",
            "#ffd57c"
          ], //中心奖盘的颜色，默认7彩(可选)
          title: {
            color: "#5c1e08",
            font: "19px Arial"
          } // 奖品标题样式(可选)
        });
        // 初始化转盘
        this.wheelSurf.init();
      },
      complete: function() {
        console.log("completed");
      }
    });
  },
  methods: {
    getData: function() {
      var num = this.initData2.list.reduce((acc, cur) => {
        return acc + cur.weight;
      }, 0);
      var weight = Math.random() * num;
      var totalweight = 0;
      // 此处initData2需在服务端处理，由服务端返回具体的initData2.list[i]
      for (var i = 0, l = this.initData2.list.length; i < l; i++) {
        totalweight += this.initData2.list[i].weight;
        if (weight <= totalweight) {
          return this.initData2.list[i];
        }
      }
    },
    start: function() {
      var v = document.getElementById("email");
      var myreg = /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/;
      if (v.value == "") {
        this.$message({
          showClose: true,
          message: "请输入邮箱",
          type: "error"
        });
        v.focus();
        return;
      }
      if (!myreg.test(v.value)) {
        this.$message({
          showClose: true,
          message: "该邮箱不合法",
          type: "error"
        });
        v.focus();
        return;
      }
      if (!this.throttle) {
        return;
      }
      this.throttle = false;
      $("#message").html("");
      $.ajax({
        method: "post",
        url: "/url",
        success: resp => {},
        error: err => {
          console.log("error start");
          var winData = this.getData(); // 正常情况下获奖信息应该由后台返回

          var count = 0;
          // 计算奖品角度

          for (const key in this.list) {
            if (this.list.hasOwnProperty(key)) {
              if (winData.id == this.list[key].id) {
                break;
              }
              count++;
            }
          }

          // 转盘抽奖，
          this.wheelSurf.lottery(count * this.angel + this.angel / 2, () => {
            this.$notify({
              title: "恭喜",
              message: "您获得：" + winData.name,
              duration: 0,
              type: "success",
              position: "top-left"
            });
            this.throttle = true;
          });
        },
        complete: function() {
          console.log("completed");
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.lottery {
  position: relative;
  display: inline-block;
  flex: 1;
  img {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -76px;
    margin-top: -82px;
    cursor: pointer;
  }
}

#message {
  position: absolute;
  top: 0px;
  left: 10%;
}
</style>



